  font-family: dosis;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
/*
  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: dosis;
}
/*
    2. Remove default margin
  */
* {
  margin: 0;
}
/*
    Typographic tweaks!
    3. Add accessible line-height
    4. Improve text rendering
  */
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
/*
    5. Improve media defaults
  */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
/*
    6. Remove built-in form typography styles
  */
input,
button,
textarea,
select {
  font: inherit;
}
/*
    7. Avoid text overflows
  */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
/*
    8. Create a root stacking context
  */
#root,
#__next {
  isolation: isolate;
}
body {
  background-image: linear-gradient(0deg, #5c0220 0%, #037773 100%);
  background-repeat: no-repeat;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
}
.title {
  text-align: center;
  color: #ffffff;
  text-shadow: 0px 0px 6px rgba(255, 255, 255, 0.7);
}
.input-container {
  color: #ffffff;
  text-shadow: 0px 0px 6px rgba(255, 255, 255, 0.7);
}
.content {
  display: flex;
  flex-direction: column;
  gap: 20px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  position: relative;
}
.content > div {
  margin: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  font-size: 20px;
  background-color: rgba(0, 0, 0, 0.4);
  color: #ffffff;
  text-shadow: 0px 0px 6px rgba(255, 255, 255, 0.7);
  padding: 5px;
  border-radius: 10%;
}

/* //:start */
/* HTML: <div class="loader"></div> */
.loader {
  min-width: 80px;
  min-height: 40px;
  border-radius: 100px 100px 0 0;
  position: relative;
  overflow: hidden;
}
.loader:before {
  content: "";
  position: absolute;
  inset: 0 0 -100%;
  background: radial-gradient(farthest-side, #ffd738 80%, #0000) left 70% top
      20%/15px 15px,
    radial-gradient(farthest-side, #020308 92%, #0000) left 65% bottom 19%/12px
      12px,
    radial-gradient(farthest-side, #ecfefe 92%, #0000) left 70% bottom 20%/15px
      15px,
    linear-gradient(#9eddfe 50%, #020308 0);
  background-repeat: no-repeat;
  animation: l5 2s infinite;
}
@keyframes l5 {
  0%,
  20% {
    transform: rotate(0);
  }
  40%,
  60% {
    transform: rotate(0.5turn);
  }
  80%,
  100% {
    transform: rotate(1turn);
  }
}
